<template>
<div>
    <el-row>
        <el-col :span="10">
            <div class="grid-content bg-purple">
                <el-tabs>
                    <el-tab-pane label="员工">
                        <el-table :data="userTableData" border style="width: 100%;height:100%">
                            <el-table-column prop="employeeId" label="员工ID"></el-table-column>
                            <el-table-column prop="name" label="员工名"></el-table-column>
                            <el-table-column prop="phoneNum" label="电话号码"></el-table-column>
                            <el-table-column prop="text" label="备注"></el-table-column>
                            <el-table-column prop="userid" label="操作">
                                <template slot-scope="scope">
                                     <el-button type="primary" size="small" @click="checkoutFinal(scope.row)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-tab-pane>
                      <el-pagination background layout="prev, pager, next" @current-change="changePage" :total="50">
                        </el-pagination>
                </el-tabs>
            </div>
        </el-col>
        <el-col :span="10">
            <div class="grid-content bg-purple-light">
                <el-tabs>
                    <el-tab-pane label="添加员工" >                                       
                         <el-main><register></register></el-main>              
                    </el-tab-pane>
                </el-tabs>
            </div>
        </el-col>

        
    </el-row>
</div>
</template>

<script>
import {
    queryVip,
    setDiscount,
    queryDiscount
} from '../api/vip'

import {
   queryEmployees
} from '../api/employee'


import register from '../components/register'
export default {
    data() {
        return {
            userTableData: [],
            cut: 0,
            dialogFormVisible: false,
            formLabelWidth: '60px',
            form: {
                cut: 0
            },

        }
    },
    components: {register},
    computed: {

    },
    methods: {
        setCut() {
            let cut = this.form.cut
            if (cut > 0 && cut < 1) {
                setDiscount(cut).then((res) => {
                    if (res.data.status == 1) {
                        alert("修改成功！")
                        this.dialogFormVisible = false
                        queryDiscount().then((res) => {
                            if (res.data.status == 1) {
                                this.cut = res.data.discount
                            }
                        })
                    }
                })
            } else {
                alert("请设置正确的折扣！")
            }
        },
        changePage(val) {
            //每页显示多少条数据
            let pageSize = 10;
            // alert(val)
        }
    },
    created: function () {
        // `this` 指向 vm 实例
        queryEmployees().then((res) => {
            this.userTableData = res.data.employees
        })

        queryDiscount().then((res) => {
            if (res.data.status == 1) {
                this.cut = res.data.discount
            }
        })

    }
}
</script>

<style scoped>
#pane-0{
    height: 550px;
}
</style>



