<template>
    <div class="pos">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

          <el-form-item label="姓名" prop="name">
            <el-col :span="24">  <el-input v-model="ruleForm.name" placeholder="不能为空"></el-input></el-col>
          </el-form-item>

         <el-form-item label="性别" prop="gender">
         <el-radio v-model="ruleForm.gender" label=0>男</el-radio>
         <el-radio v-model="ruleForm.gender" label=1>女</el-radio>
         </el-form-item>

           <el-form-item label="手机号码" prop="tel">
            <el-col :span="24"> <el-input  v-model="ruleForm.tel" placeholder="长度为11位，满足号码规则"></el-input></el-col>
          </el-form-item>
           <el-form-item label="备注" prop="text">
            <el-col :span="24">  <el-input v-model="ruleForm.text" placeholder="不能为空"></el-input></el-col>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-col :span="24"><el-input type="password" v-model="ruleForm.password" autocomplete="off" show-password placeholder="长度为8-16位"></el-input></el-col>
          </el-form-item>

          <el-form-item label="确认密码" prop="checkPass">
            <el-col :span="24"><el-input type="password" v-model="ruleForm.checkPass" autocomplete="off" show-password></el-input></el-col>
          </el-form-item>

          <el-form-item>
            <el-button type="primary"  round @click="submitForm('ruleForm')">立即添加</el-button>
            <el-button type="primary" round  @click="resetForm('ruleForm')">&nbsp;重置&nbsp;</el-button>
          </el-form-item>
    </el-form>
  </div>
</template>
<style scoped>
    .red{color: red;font-size: 16px;}
    .pos{position: absolute;left: 40%;top:20%;}
    p{font-size: 20px;}
    span{cursor:pointer;}
    span:hover{color:#41b883;}
    select{font-size: 16px;display:block; width:80px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 10px; margin-right: 175px;outline:none; border:1px solid #888;  box-sizing:border-box;}
</style>

<script scoped>
export default {
  data () {
    var checkTel = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'))
      }
      setTimeout(() => {
        // 匹配13，14，15，16,17,18开头的手机号码！
        var reg = /^0?1[3|4|5|6|7|8][0-9]\d{8}$/
        if (!reg.test(value)) {
          callback(new Error('请输入正确的手机号码！'))
        } else {
          callback()
        }
      }, 1000)
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        username: '',
        name: '',
        gender: '',
        tel: '',
        password: '',
        checkPass: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 1, max: 12, message: '长度在 1 到 12 个字符', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        gender: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ],
        tel: [
          { required: true, validator: checkTel, trigger: 'blur' }
        ],
        password: [
          { required: true, validator: validatePass, trigger: 'blur' },
          { min: 8, max: 16, message: '长度在 8 到 16位', trigger: 'blur' }
        ],
        checkPass: [
          {required: true, validator: validatePass2, trigger: 'blur' }
        ]
      }
    }
  },

  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("添加成功!")
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>



